import "./style.scss";

import sound from "./assets/walk.mp3";

const audio = new Audio(sound);

function playAudio() {
  //audioPlayer.play();
  audio.play();
}
function pauseAudio() {
  //audioPlayer.pause();
  audio.pause();
}

(function () {
  var sprite = document.querySelector(".sprite"),
    key = { left: false, right: false },
    trans = 0,
    property = getTransformProperty(sprite);

  function getTransformProperty(element) {
    var properties = [
      "transform",
      "WebkitTransform",
      "msTransform",
      "MozTransform",
      "OTransform",
    ];
    var p;
    while ((p = properties.shift())) {
      if (typeof element.style[p] != "undefined") {
        return p;
      }
    }
    return false;
  }

  function translate() {
    sprite.style[property] = "translateX(" + trans + "px)";
  }

  function walk(e) {
    var keyCode = e.keyCode;
    if (keyCode === 39) {
      //e.preventDefault()
      scroll(e);
      key.right = true;
      playAudio();
      stallInteraction(windowWidth);
    } else if (keyCode === 37) {
      //e.preventDefault()
      scroll(e);
      key.left = true;
      playAudio();
      stallInteraction(windowWidth);
    }
    if (key.right === true) {
      //trans += 10;
      translate();
      sprite.classList.remove("left");
      sprite.classList.add("right");
      sprite.classList.add("walk-right");
      sprite
        .getElementsByClassName("image-wrapper")[0]
        .classList.add("walking");
      sprite
        .getElementsByClassName("image-wrapper")[0]
        .classList.remove("walking-reverse");
    } else if (key.left === true) {
      //trans -= 10;
      translate();
      sprite.classList.remove("right");
      sprite.classList.add("left");
      sprite.classList.add("walk-left");
      sprite
        .getElementsByClassName("image-wrapper")[0]
        .classList.remove("walking");
      sprite
        .getElementsByClassName("image-wrapper")[0]
        .classList.add("walking-reverse");
    }
  }

  function stop(e) {
    pauseAudio();

    var keyCode = e.keyCode;
    if (keyCode === 39) {
      key.right = false;
      sprite
        .getElementsByClassName("image-wrapper")[0]
        .classList.remove("walking");
    } else if (keyCode === 37) {
      key.left = false;
      sprite
        .getElementsByClassName("image-wrapper")[0]
        .classList.remove("walking-reverse");
    }
    if (key.right === false) {
      sprite.classList.remove("walk-right");
    }
    if (key.left === false) {
      sprite.classList.remove("walk-left");
    }
  }

  document.addEventListener("keydown", walk, false);
  document.addEventListener("keyup", stop, false);
})();

var element = document.querySelector(".swiper-container");
var backgroundMove;

$(document).on("keyup", function (e) {
  clearInterval(backgroundMove);
});

function right1() {
  if (backgroundMove) {
    clearInterval(backgroundMove);
  }
  backgroundMove = setInterval(function () {
    element.scrollLeft += 10;
  }, 100);
}
function left1() {
  if (backgroundMove) {
    clearInterval(backgroundMove);
  }
  backgroundMove = setInterval(function () {
    element.scrollLeft -= 10;
  }, 100);
}

function scroll(e) {
  console.log("called");
  e.preventDefault();
  const keyScrollMapping = {
    37: -100,
    39: 100,
  };

  if (keyScrollMapping.hasOwnProperty(e.keyCode)) {
    element.scrollBy({
      top: 0,
      left: keyScrollMapping[e.keyCode] / 10,
      behavior: "smooth",
    });
  }
}

$(".swiper-container").focus();

const windowWidth = window.innerWidth;
const swiperSlides = document.querySelectorAll(".swiper-slide");

function stallInteraction(windowWidth) {
  const swiperSlides = document.querySelectorAll(".swiper-slide");

  swiperSlides.forEach((slide) => {
    const slideInteraction = slide.querySelector(".stall-interaction");
    const position = slide.getBoundingClientRect();

    const xPosition = position.left;

    const windowWidthMiddle = windowWidth / 2;

    if (
      (xPosition < windowWidthMiddle + 150) &
      (xPosition > windowWidthMiddle - 200)
    ) {
      //slideInteraction.style.display = "flex";
      slideInteraction.classList.add("visible");
      slideInteraction.parentElement.classList.add("active");
    } else {
      if (slideInteraction) {
        //slideInteraction.style.display = "none";
        slideInteraction.classList.remove("visible");
        slideInteraction.parentElement.classList.remove("active");
      }
    }
  });
}
